<template>
  <Row v-if="!isEmpty(time) " class="NewcomerBenefits-MeasureTime" y="c">
    <Column class=" box radius-4 position-r " :class="isCloseTimer ? 'bg-white-50' : 'bg-primary'" x="c" y="c">
      <span class="label-sm bold c-white num-f">{{ (differenceTime).format('DD') }}</span>
    </Column>
    <span class="m-lr-6 label-sm c-info">D</span>
    <Column class="box radius-4 position-r " :class="isCloseTimer ? 'bg-white-50' : 'bg-primary'" x="c" y="c">
      <span class="label-sm bold c-white num-f">{{ (differenceTime).format('HH') }}</span>
    </Column>
    <span class="m-lr-10 label-lg c-info">:</span>
    <Column class="box radius-4 position-r " :class="isCloseTimer ? 'bg-white-50' : 'bg-primary'" x="c" y="c">
      <span class="label-sm bold c-white num-f">{{ (differenceTime).format('mm') }}</span>
    </Column>
    <span class="m-lr-10 label-lg c-info">:</span>
    <Column class="box radius-4 position-r " :class="isCloseTimer ? 'bg-white-50' : 'bg-primary'" x="c" y="c">
      <span class="label-sm bold c-white num-f">{{ (differenceTime).format('ss') }}</span>
    </Column>
  </Row>
  <!-- {{ isCloseTimer }} -->
  <!-- <slot v-if="isCloseTimer" name="close" /> -->
</template>
<script setup>

const props = defineProps({
  time: {},
  size: {
    default: 38
  },
  open: {
    default: true,
  }
})

const now = $ref(Date.now())
let difference = $ref(0)
// const n = 1 * 60 * 60
const differenceTime = $computed(() => {
  if (props.time < now) {
    return dayjs.duration(0, 'millisecond')
  } else {
    return dayjs.duration(props.time - now - (difference), 'millisecond')
  }
})

let isCloseTimer = $ref(false)
let timer
const goRunTimer = () => {
  if (props.open == false) {
    isCloseTimer = true
    return
  }

  timer = runTimer(() => {
    try {
      if (isCloseTimer == true) {
        return false
      }
      difference = difference + 1000
      const flag = (props.time - now - difference) > 0
      if (flag == false) {
        isCloseTimer = true
      }

      // console.log('flag', flag)
      return flag
    } catch (e) {
      console.error('ee', e)
    }
  }, 1000, true)
}

const size = $computed(() => props.size + 'px')

onBeforeMount(() => {
  goRunTimer()
})
onBeforeUnmount(() => {
  timer = null
  isCloseTimer = true
})
</script>
<style lang="scss">
.NewcomerBenefits-MeasureTime{
  .box{
    height: 38px;
    min-width: 38px;
  }
}
</style>
