<template>
  <div ref="fatherDom" class="father " :style="{width:width+'px'}">
    <div ref="childDom" class="child">
      {{ num }}
    </div>
  </div>
</template>
<script setup>
import { onUpdated } from 'vue'

const fatherDom = $ref(null)
const childDom = $ref(null)

const props = defineProps({
  width: {},
  num: {}
})

const upadateNum = () => {
  const rate = fatherDom?.offsetWidth / childDom?.offsetWidth
  if (rate <= 1) {
    childDom.style.transform = `scale(${rate * 0.9})`
  } else {
    childDom.style.transform = 'scale(1)'
  }
}

onMounted(() => {
  upadateNum()
  setTimeout(() => {
    upadateNum()
  }, 200)
})

onUpdated(() => {
  upadateNum()
})

watch(
  () => [props.width],
  () => {
    setTimeout(() => {
      upadateNum()
    }, 200)
  }
)
</script>
<style lang="scss">
.father{
  white-space: nowrap;
  overflow: hidden;
  display: flex;
  justify-content: center;
  .child{
    white-space: nowrap;
    display: flex;
    justify-content: center;
  }
}
</style>
