
<template>
  <van-overlay
    class="DiaBusinessCard f-center f-column f-sb"
    :show="modelValue"
    :lock-scroll="false"
    :z-index="110"
  >
    <Column class="f-1" y="c">
      <el-image class="w-650 h-870 m-t-8-" :src="imgBase64">
        <template #error>
        <!-- <Row class="w-300 h-534" x="c" y="c">
            ...
          </Row> -->
        </template>
      </el-image>
    </Column>
    <Column class="b h-330 w-100b bg-white">
      <Row class="w-100b h-100 p-lr-30" x="sb">
        <span class="font-30 bold">{{ $t('activity.forwardActivityTitle2') }}</span>
        <img class="w-24 h-24 " src="@/image/user/x.webp" alt="" @click="onClose">
      </Row>
      <Row class="w-100b p-lr-30 p-t-30">
        <Column class="m-r-40" x="c" y="c">
          <img src="@/image/user/copy-link.webp" class="w-72 h-72 pointer" @click="onCopyToClip">
          <span class="m-t-28 label-sm">{{ $t('activity.copyLink') }}</span>
        </Column>
        <Column class="" x="c" y="c">
          <img src="@/image/user/download-image.webp" :loading="loading" class="w-72 h-72 pointer" @click="submit">
          <span class="m-t-28 label-sm">{{ $t('activity.downloadImages') }}</span>
        </Column>
      </Row>
    </Column>
    <!-- <Column class="w-570 p-lr-20 h-300 bg-white radius-10 position-r" y="c" x="c" @click.stop>
      <Row x="c" y="c" class="title m-b-40 w-100b font-36">{{ data }}</Row>
      <van-button type="primary" @click="() => winMes.postParent('toHome')">{{ $t('form.toHome') }}</van-button>
    </Column> -->
  </van-overlay>
</template>
<script setup>
import { Swiper, SwiperSlide, useSwiper } from 'swiper/vue'
import { Navigation } from 'swiper'
import 'swiper/css'
import 'swiper/css/navigation'
import QRcode from 'jr-qrcode'
import playbillImage from '@/image/user/playbill.webp'
import incentivesImage from '@/image/user/incentives.webp'

const emit = defineEmits()
const props = defineProps({
  modelValue: {
    default: false
  },
  close: {},
  link: {},
  num: {},
  submited: {},
})

const languageStore = useLanguageStore()

let show = $ref(props?.modelValue ?? false)
let loading = $ref(false)

let imgBase64 = $ref('')

let myCodeImage = $ref()

const onCopyToClip = () => {
  copyToClip(props.link)
  showNotify({ type: 'success', message: $t('alert.copied') })

  // ElMessage.success($t('alert.copied'))
}

const getImage = (imageUrl) => {
  return new Promise((resolve, reject) => {
    const link = $f.getAssetsFile(imageUrl)
    const image = new Image()

    image.setAttribute('crossOrigin', 'anonymous')
    image.src = link

    image.onload = async () => {
      resolve(image)
    }
  })
}

const handleImg = (imgSrc) => {
  const image = new Image()

  image.setAttribute('crossOrigin', 'anonymous')
  image.onload = async () => {
    const canvas = document.createElement('canvas')
    canvas.width = image.width
    canvas.height = image.height
    const getX = (n) => image.width / 340 * n
    const getY = (n) => image.height / 456 * n
    const context = canvas.getContext('2d')
    context.drawImage(image, 0, 0, image.width, image.height)

    const fontFamilyMap = {
      [LANGEN + 'ios']: 'Helvetica Neue',
      [LANGCN + 'ios']: 'PingFang SC',
      [LANGTW + 'ios']: 'PingFang SC',
      [LANGEN + 'android']: 'Roboto',
      [LANGCN + 'android']: 'Droidsansfallback',
      [LANGTW + 'android']: 'Droidsansfallback',
    }
    const fontFamily = fontFamilyMap[languageStore.locale + platform.toLowerCase()]
    console.log('fontFamily', languageStore.locale, platform.toLowerCase(), fontFamily)
    if (languageStore.locale == LANGEN) {
      context.drawImage(await getImage(incentivesImage), getX(20), getY(66), getX(22), getX(22))
      context.fillStyle = '#06F0C6'
      context.font = `${getX(20)}px ${fontFamily}`
      context.fillText($t('activity.timeNewbieBenefits2'), getX(46), getY(86))
      context.fillStyle = '#fff'
      if (props.num.toString().length > 2) {
        context.font = `bold ${getX(25)}px ${fontFamily}`
        context.fillText($t('activity.unlockNow'), getX(22), getY(126))
        context.fillStyle = '#06F0C6'
        context.fillText(`${props.num} USDT`, getX(176), getY(126))
      } else {
        context.font = `bold ${getX(29)}px ${fontFamily}`
        context.fillText($t('activity.unlockNow'), getX(22), getY(126))
        context.fillStyle = '#06F0C6'
        context.fillText(`${props.num} USDT`, getX(200), getY(126))
      }

      context.fillStyle = 'rgba(255,255,255,0.5)'
      context.font = `${getX(12)}px ${fontFamily}`
      context.fillText($t('activity.servicePlatform'), getX(26), getY(412))
      context.fillText($t('activity.servicePlatform2'), getX(26), getY(430))
      context.drawImage(myCodeImage, getX(264), getY(380), getX(60), getX(60))
    } else {
      context.drawImage(await getImage(incentivesImage), getX(108), getY(66), getX(22), getX(22))
      context.fillStyle = '#06F0C6'

      context.font = `${getX(20)}px ${fontFamily}`
      context.fillText($t('activity.timeNewbieBenefits2'), getX(134), getY(86))
      context.fillStyle = '#fff'
      context.font = `bold ${getX(29)}px ${fontFamily}`
      context.fillText($t('activity.unlockNow'), getX(50), getY(126))
      context.fillStyle = '#06F0C6'
      context.fillText(`${props.num} USDT`, getX(176), getY(126))
      context.fillStyle = 'rgba(255,255,255,0.5)'
      context.font = `${getX(12)}px ${fontFamily}`
      context.fillText($t('activity.servicePlatform'), getX(26), getY(424))
      context.drawImage(myCodeImage, getX(264), getY(380), getX(60), getX(60))
    }

    canvas.toBlob(() => {
      imgBase64 = canvas.toDataURL('image/jpg')
    })
  }
  image.src = imgSrc
}

const onClose = () => {
  show = false
  if (props?.close) {
    props?.close()
  }
  emit('close', false)
  emit('update:modelValue', false)
}

const submit = async () => {
  try {
    loading = true
    winMes.postParent('downloadImage', { image: imgBase64 })
    // downloadImage(imgBase64)
  } finally {
    loading = false
  }
}

onMounted(() => {
  nextTick(() => {
    myCodeImage = new Image()
    myCodeImage.src = QRcode.getQrBase64(props.link)// 
    myCodeImage.onload = () => {
      handleImg($f.getAssetsFile(playbillImage))
    }
  })
})

watch(() => props.modelValue, (v) => {
  show = v
  if (show == true) {
  }
})

</script>

<style lang="scss">
@use "sass:math";
@use "sass:map";
@use '@/sass/var/index.scss' as *;

.DiaBusinessCard{
  .b{
    border-radius: 8px 8px 0px 0px;
  }
}
</style>
