        <Column class="f-1 position-r ">
          <Row v-if="index != stepsData.length - 1" class="xian  dash-underline-y" :class="stepActive >= index ? 'bg-primary' : 'dash-underline-y'" />
          <Row class="w-100b h-92 position-r bg-white radius-8 p-l-32 m-b-30 border border-w-2" :class="stepActive == index ? ' border-c-primary' : 'c-d border-c-white'" y="c">
