<script setup>
import DiaBusinessCard from '@/view/activity/components/DiaBusinessCard.vue'
import DiaGetReward from '@/view/activity/components/DiaGetReward.vue'
import DiaToAudit from '@/view/activity/components/DiaToAudit.vue'
import DiaRewardEnd from '@/view/activity/components/DiaRewardEnd.vue'
import FlipCard from '@/view/activity/components/FlipCard/FlipCard.vue'
import MTime from '@/view/activity/components/MTime.vue'
import DiaTurnoverOrder from '@/view/activity/components/DiaTurnoverOrder.vue'
import 'animate.css'

const userStore = useUserStore()
const languageStore = useLanguageStore()
const route = useRoute()
const verifiedAudit = $computed(() => {
  return userStore?.info?.verifiedAudit
})

const swiperDomId = `swiper-${randomString()}`

const isClosed = $computed(() => Date.now() > data.endTime)

let loading = $ref(false)

const toRouterMap = {
  1: () => winMes.postParent('toRegister'),
  2: () => winMes.postParent('toRecharge'),
  3: () => winMes.postParent('toTrade'),
  4: () => winMes.postParent('toContract'),
}

const missionStatement = $computed(() => {
  if (isEmpty(stepsData[stepActive])) {
    return ''
  }
  const map = {
    0: () => $t.replace('activity.missionStatement1', { n: data?.rewardBig ?? 0 }),
    1: () => $t.replace('activity.missionStatement2', { n: stepsData[stepActive]?.depositAmount ?? 0 }),
    2: () => stepsData[stepActive]?.tradeAmount != 0 ? $t.replace('activity.missionStatement3', { n: stepsData[stepActive]?.tradeAmount ?? 0 }) : $t.replace('activity.missionStatement32', { }),
    3: () => stepsData[stepActive]?.tradeAmount != 0 ? $t.replace('activity.missionStatement4', { n: stepsData[stepActive]?.tradeAmount ?? 0 }) : $t.replace('activity.missionStatement42', { }),
  }
  // return map[stepActive]()
  return stepsData[stepActive]?.describe
})

const stepActive = $ref(0)
let data = $ref({})
let registrationLink = $ref('')

let stepsData = $ref([{}, {}, {}, {}])

const toIncentives = () => {
  if (!userStore.token) {
    return
  }
  const query = reserveUrlQuery(route)
  router.push({
    path: '/activity/incentives',
    query: {
      ...query,
    },
  })
}

const toShare = () => {
  DiaSingle({
    modelValue: true,
    com: DiaBusinessCard,
    link: registrationLink,
    num: data?.rewardBig,
    submited: async (v) => {
    }
  })
}

const toClaim = () => {
  if (![0, 1, 2, 4].includes(verifiedAudit)) {
    DiaSingle({
      modelValue: true,
      com: DiaToAudit,
      typeText: $t('userCenter.order'),
      submited: () => {
        ElMessage.success($t('alert.success'))
      },
    })
  } else {
    DiaSingle({
      modelValue: true,
      num: data.withdrawalReward,
      com: DiaGetReward,
      typeText: $t('userCenter.order'),
      submited: () => {
        setTimeout(() => {
          getData()
        }, 300)
        ElMessage.success($t('alert.success'))
      },
    })
  }
}

const onExtract = ({ progressType }) => {
  const s = stepsData.find(e => e.progressType == progressType)
  s.isReceiveReward = true
  setTimeout(() => {
    getData()
  }, 300)
}

const onDrawCard = async () => {
  try {
    loading = true
    let sa = stepActive
    if (stepActive == 3) {
      sa = 2
    }
    if (sa != 0 && stepsData[sa - 1].isReceiveReward == false) {
      DiaSingle({
        modelValue: true,
        com: DiaTurnoverOrder,
        submited: () => {
        // getPosition()
        },
      })
      return
    }
    await activityApi.getWoolPartyUser({})
    DiaSingle({
      com: FlipCard,
      keyid: `${userStore?.info?.uid}-${data.activityId}-${stepsData[stepActive]?.progressId}-${stepsData[stepActive]?.progressType}`,
      disabled: isClosed || data?.status == 2,
      activityId: data?.activityId,
      isCompleted: stepsData[stepActive]?.isCompleted,
      progressId: stepsData[stepActive]?.progressId,
      progressType: stepsData[stepActive]?.progressType,
      rewardAmount: stepsData[stepActive]?.rewardAmount,
      isReceiveReward: stepsData[stepActive]?.isReceiveReward,
      onExtract: onExtract,
    // submited: () => {
    //   ElMessage.success($t('alert.success'))
    //   // getPosition()
    // },
    })
  } finally {
    loading = false
  }
}

const onRewardEnd = () => {
  DiaSingle({
    modelValue: true,
    com: DiaRewardEnd,
    submited: () => {
      // getPosition()
    },
  })
}

const getData = async () => {
  const d = await activityApi.queryHome({})
  console.log('getData', d)

  data = d
  stepsData = d.newcomerActivityProgressList ?? []

  if (userStore.token && data.status == 2) {
    onRewardEnd()
  }
}

let questionData = $ref([])

// question
const getQuestion = async () => {
  const body = {}
  const d = await activityApi.question(body)
  console.log('getQuestion', d)
  questionData = d.map(item => {
    const dd = item.reduce((o, e) => {
      return {
        ...o,
        [e.language]: {
          issue: e.issue,
          solution: e.solution,
        },
      }
    }, {})
    return {
      ...dd,
      isOpen: false,
    }
  })
}

// 
const contractInfo = async () => {
  const d = await userApi.inviteInfo({
  })
  registrationLink = d.inviteLink
}
const onClickLeft = () => {
  // router.go(-1)
  winMes.postParent('toHome')
}
winMes.postParent('hiddenNaviBar')

onBeforeMount(() => {
  getData()
  getQuestion()
  if (userStore.token) {
    contractInfo()
  }
  // onDrawCard()
})

</script>
