<template>
  <el-dialog
    class="dialog-d radius-20 flip-card"
    modal-class="dialog-m-center anuc-out-no"
    :modelValue="show"
    :destroy-on-close="false"
    :show-close="false"
    append-to-body
    @close="onClose"
  >
    <template #default>
      <Column class="w-100b h-600 position-r  radius-20" x="c" y="c">
        <Row v-if="!isFlip" class="font-40 c-white bold position-a top-40 animate__animated animate__fadeIn">{{ $t('activity.drawCard') }}</Row>
        <Row v-if="isFlip1s" class="font-40 c-white bold position-a top-0 animate__animated animate__fadeIn"> {{ $t.replace('activity.CongratulationsGet', { n: num ?? 0 }) }}</Row>
        <Row class="w-648 h-474 position-a -top-60" :class="className" />
        <Row class="w-648 " :x="'sb'">
          <FlipCardItem
            v-model:publishIndex="publishIndex"
            v-model:num="num"
            :activityId="activityId"
            :index="3"
            :keyid="keyid"
            :disabled="disabled || isFlip"
            :frontUrl="qMarkImage"
            :backUrl="backFlipImage"
            :isCompleted="isCompleted"
            :isReceiveReward="isReceiveReward"
            :progressId="progressId"
            :progressType="progressType"
            :rewardAmount="rewardAmount"
            class="FlipCardItem"
            @onChange="onChange($event,0)"
          />
          <FlipCardItem
            v-model:publishIndex="publishIndex"
            v-model:num="num"
            :activityId="activityId"
            :index="2"
            :keyid="keyid"
            :disabled="disabled || isFlip"
            :frontUrl="qMarkImage"
            :backUrl="backFlipImage"
            :isCompleted="isCompleted"
            :isReceiveReward="isReceiveReward"
            :progressId="progressId"
            :progressType="progressType"
            :rewardAmount="rewardAmount"
            class="FlipCardItem"
            @onChange="onChange($event,1)"
          />
          <FlipCardItem
            v-model:publishIndex="publishIndex"
            v-model:num="num"
            :activityId="activityId"
            :index="3"
            :keyid="keyid"
            :disabled="disabled || isFlip"
            :frontUrl="qMarkImage"
            :backUrl="backFlipImage"
            :isCompleted="isCompleted"
            :isReceiveReward="isReceiveReward"
            :progressId="progressId"
            :progressType="progressType"
            :rewardAmount="rewardAmount"
            class="FlipCardItem"
            @onChange="onChange($event,2)"
          />
        </Row>
        <el-button v-if="isFlip1s" class="w-420 h-88 radius-44 animate__animated animate__fadeIn font-28 position-a -bottom-60" type="primary" @click="show=false">{{ $t.replace('activity.gotIt', { n: seconds ?? 0 }) }}</el-button>
      </Column>
    </template>

  </el-dialog>
</template>
<script setup>
import FlipCardItem from './FlipCardItem.vue'
import qMarkImage from '@/image/activity/qMark.webp'
import backFlipImage from '@/image/activity/backFlip.webp'
import 'animate.css'

const props = defineProps({
  keyid: {},
  disabled: {},
  activityId: {},
  isCompleted: {},
  progressId: {},
  progressType: {},
  rewardAmount: {},
  isReceiveReward: {},
  modelValue: {
    default: true
  },
  close: {},
  onExtract: {},
})
let className = $ref('')
let show = $ref(props?.modelValue)

let publishIndex = $ref(props.isReceiveReward ? localStorage[`${props.keyid}-publishIndex`] ?? 1 : '')
const num = $ref('')
let isFlip = $ref(props.isReceiveReward ?? false)

const initPublishIndex = () => {
  if (props.isFlip == true) {
    publishIndex = localStorage[`${props.keyid}-publishIndex`] ?? 1
  }
}

const onChange = async (v, k) => {
  const doms = document.getElementsByClassName('FlipCardItem')
  const arr = [0, 1, 2]
  const hArr = arr.filter(i => i !== k)
  hArr.forEach((i) => {
    doms[i].classList.add('fc-hidden')
  })

  if (k === 2)doms[2].classList.add('fc-right-center')
  if (k === 0)doms[0].classList.add('fc-left-center')

  isFlip = v
  props.onExtract({ progressType: props.progressType })
}

const onClose = () => {
  props.close()
}

onMounted(() => {
  initPublishIndex()
})

let isFlip1s = $ref(false)
let seconds = $ref(5)

watch(
  () => isFlip,
  () => {
    setTimeout(() => {
      className = 'choosed-card-bg animate__animated animate__fadeIn'
      isFlip1s = true
      runTimer(() => {
        seconds--
        return seconds != 0
      }, 1000)
    }, 1000)
  }
)

watch(
  () => seconds,
  () => {
    if (seconds < 1) {
      show = false
    }
  }
)

</script>

<style lang="scss">
.flip-card{
  border-radius: 20px !important;
  margin-left: 0px;
  margin-right: 0px;
  width: 648px !important;
  .el-loading-mask{
    border-radius: 20px;
  }
  --el-bg-color:#ffffff00;
  --el-box-shadow:#ffffff00
}
.anuc-out-no{
  .el-dialog__header{
    padding:0px !important;
    width: 0px !important;
    height: 0px !important;
  }
  .el-dialog__body{
    padding:0px !important;
    height:100%;
    width:100%;
  }
}
.anuc-close{
  width: 56px;
  height: 56px;
  position: relative;
  top: 100px;
}
.fc-hidden {

    transform: scale(0);
    transition: all 1s ease; /* 1 */
}
.FlipCardItem{
  transition: all 1s ease;
}
.fc-right-center {
  transform: translate(-2.96rem,0);
}

.fc-left-center {
  transform: translate(2.96rem,0);
}
.choosed-card-bg{
  background: url('@/image/activity/choosedCardBg.webp');
  background-size: cover;
  background-repeat: no-repeat;
}

</style>
