<template>
  <el-dialog
    class="dialog-d DiaRewardEnd"
    :modelValue="isRunSubmit ? false : show"
    modal-class="dialog-m-center "
    width="87%"
    :close-on-click-modal="false"
    :destroy-on-close="true"
    :show-close="false"
    :append-to-body="true"
    @close="onClose"
  >
    <template #header>
      <Row x="s">
        <span class="font-40 bold">{{ $t('alert.hint') }}</span>
      </Row>
    </template>
    <Row class="m-b-40" y="c">
      <Row class="label-lg c-info">{{ $t('activity.reminderOrder') }}</Row>
    </Row>

    <Row class="w-100b" x="e">
      <el-button class="w-100b font-w-400 h-80 label-lg" type="primary" @click="onClose">{{ $t('vote.gotIt') }}</el-button>
    </Row>
  </el-dialog>
</template>
<script setup>
const languageStore = useLanguageStore()

const emit = defineEmits()
const props = defineProps({
  modelValue: {},
  submited: {}
})

let show = $ref(props?.modelValue ?? false)
let loading = $ref(false)

const onClose = () => {
  show = false
  if (props?.close) {
    props?.close()
  }
  emit('close', false)
  emit('update:modelValue', false)
}

const submit = async () => {
  try {
    winMes.postParent('toHome')
    props?.submited()
    show = false
    emit('submited')
  } finally {
    loading = false
  }
}

const noMoreTips = () => {
  futuresStore.takeCloseConfirmation = !futuresStore.takeCloseConfirmation
}

if (props.isRunSubmit) {
  submit()
}

defineExpose({ submit })

watch(() => props.modelValue, (v) => {
  show = v
})

</script>

<style lang="scss">
.DiaRewardEnd{
  width: 650px !important;
  border-radius: 20px !important;
}
</style>
