<template>
  <div>
    <div ref="cardBoxDom" class="card">
      <div class="front" :style="{ backgroundImage: 'url(' + frontUrl + ')' }" />
      <div class="back" :style="{ backgroundImage: 'url(' + backUrl + ')' }">
        <Column class="c-white w-100b">
          <Row class="w-100b position-a top-74 font-36" x="c">
            <ScaleNumber :width="54" :num="num" class="font-68" />
          </Row>
          <Row class="w-100b position-a top-178 font-24" x="c">USDT</Row>
        </Column>
      </div>
    </div>
  </div>
</template>
<script setup>
const emit = defineEmits(['onChange'])
const cardBoxDom = $ref(null)

const props = defineProps({
  index: {},
  keyid: {},
  isReceiveReward: {},
  isCompleted: {},
  activityId: {},
  publishIndex: {},
  frontUrl: {},
  backUrl: {},
  disabled: {
    default: false
  },
  progressId: {},
  progressType: {},
  rewardAmount: {},
  flipped: {},
})
const userStore = useUserStore()

let isFlip = $ref(false)
let num = $ref(props.rewardAmount ?? localStorage[`${props.keyid}-num`] ?? '')
// 
const drawPrize = async () => {
  const body = {
    progressId: props.progressId,
    progressType: props.progressType,
  }
  const d = await activityApi.drawPrize(body)
  num = d.rewardAmount
  localStorage[`${props.keyid}-publishIndex`] = props.index
  localStorage[`${props.keyid}-num`] = num
  // num = 223
  emit('update:publishIndex', props.index)
  emit('update:num', num)
}

onMounted(() => {
  // 
  cardBoxDom.addEventListener('click', function () {
    if (!userStore.token) {
      winMes.postParent('toRegister')
      return
    }
    if (!userStore.token || props.disabled || props.isCompleted == false) {
      console.log('X')
      return
    }
    cardBoxDom.classList.toggle('flipped')
    drawPrize()
    isFlip = !isFlip
    emit('onChange', isFlip)
  })
})

</script>
<style lang="scss" scoped>
.card {
    width: 205px;
    height: 268px;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 2s;
    cursor: pointer;
}

.front, .back {
    width: 100%;
    height: 100%;
    position: absolute;
    backface-visibility: hidden;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
.front {
}

.back {
    transform: rotateY(180deg);
}
.flipped {
    transform: rotateY(180deg) scale(1.4); /* Y180 */
}

</style>
