<template>
  <el-dialog
    class="dialog-d DiaGetReward"
    :modelValue="isRunSubmit ? false : show"
    modal-class="dialog-m-center light "
    width="560px"
    :close-on-click-modal="false"
    :destroy-on-close="true"
    :show-close="false"
    :append-to-body="true"
    @close="onClose"
  >
    <template #header>
      <Row x="s">
        <span class="font-40 bold">{{ $t('alert.hint') }}</span>
      </Row>
    </template>
    <Row class="m-b-40" y="c">
      <i18n class="label-lg c-info" path="activity.authentication">
        <template #c>
          <span class="c-d">{{ $t('activity.authentication2') }}</span>
        </template>
      </i18n>
    </Row>

    <Row class="w-100b" x="e">
      <el-button class="f-1 font-w-400 h-80 title-sm m-r-20 cancel-btn" :loading="loading" @click="onClose">{{ $t('form.cancel') }}</el-button>
      <el-button class="f-1 font-w-400 h-80 title-sm" type="primary" :loading="loading" @click="submit">{{ $t('alert.confirm') }}</el-button>
    </Row>
  </el-dialog>
</template>
<script setup>
const languageStore = useLanguageStore()

const emit = defineEmits()
const props = defineProps({
  modelValue: {},
  symbol: { },
  isRunSubmit: {
    default: false
  },
  num: {},
  submited: {}
})

let show = $ref(props?.modelValue ?? false)

let loading = $ref(false)

const onClose = () => {
  show = false
  if (props?.close) {
    props?.close()
  }
  emit('close', false)
  emit('update:modelValue', false)
}

const submit = async () => {
  try {
    loading = true
    winMes.postParent('toKYC')
    show = false
    emit('submited')
  } finally {
    loading = false
  }
}

const noMoreTips = () => {
  futuresStore.takeCloseConfirmation = !futuresStore.takeCloseConfirmation
}

if (props.isRunSubmit) {
  submit()
}

defineExpose({ submit })

watch(() => props.modelValue, (v) => {
  show = v
})

</script>

<style lang="scss">
.DiaGetReward{
  width: 650px !important;
  border-radius: 20px !important;
  .cancel-btn{
    border: 2px solid #DADDEA !important;
  }
}
</style>
